.wrapper {
  background-color: rgba(193, 248, 248, 0.693);
  height: 100%;
  width: 100%;
}


.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  
}

.photo {
  background-color: rgba(193, 232, 240, 0.693);
  width: 55%;
  height: 500px;
  margin: 10px;
  text-align: right;
  position: relative;
  margin-top: 100px;
}


.box {
  background-color: rgba(242, 248, 248, 0.693);
  display: flex;
  flex-direction: column;
  height: 250px;
  transition: all 3s ease;
}

.memory-photo {
  height: 90%;
  width: 70%;
  position: absolute;
  top: 20px;
  left: 10px;
  /* opacity: 0;
  transform: translateX(20px);
  animation: fadein 1s  ease-in forwards; */
}

.memory-photo1 {
  width: 50%;
  height: 70%;
  margin-top: 80px;
  /* opacity: 0; */
  /* transform: translateX(20px); */
  /* animation: fadein 1s  ease-in forwards; */
}
@keyframes fadein { 
  to{
    opacity: 0.7;
    transform: translateX(0);
  }
}

.memory-photo2 {
  width: 50%;
